				font-size: 1.5rem;
				font-family: $title-font; } } } }
// popup

// footer
.footer {
	background-color: #f9f9f9;
	padding: {
		top: 2.8rem;
		bottom: 2.8rem; }
	&-copy {
		&_text {
			display: block;
			font-size: 1.3rem;
			color: #646464;
			margin: {
				left: 1.4rem;
				right: 1.4rem; } } }
	&-author {
		display: flex;
		align-items: center;
		&_text {
			display: block;
